@mixin desktop(){
	@media (min-width: 1367px){
		@content
	}
}

@mixin ipadPro2{
	@media (max-width: 1366px){
		@content
	}
}

@mixin ipadPro{
	@media (max-width: 1024px){
		@content
	}
}

@mixin ipad2{
	@media (max-width: 922px){
		@content
	}
}

@mixin ipad{
	@media (max-width: 768px){
		@content
	}
}

@mixin iphone8{
	@media (max-width: 414px){
		@content
	}
}

@mixin iphone5{
	@media (max-width: 320px){
		@content
	}
}

.footer{
	width: 100%;
	padding: 20px 20px 0;
	background-color: #4b2a00;
	overflow: hidden;
	@include iphone8(){
		padding-bottom: 10px;
	}
}

.ftBox_one{
	width: 100%;
	height: 100px;
	display: flex;
	justify-content: center;
	border-bottom: 1px solid #ddd;
	div{
		margin: 0 20px;
	}
	
	@include iphone8(){
		height: auto;
		flex-direction: column;
		align-items: center;
		div{
			margin: 0 0 20px;
		}
	}
}
.flogo{
	width: 65px;
	height: 65px;
	@include iphone8(){
		width: 45px;
		height: 45px;
	}
	img{
		width: 100%;
	}
}
.social{
	display: flex;
	span{
		margin: 0 5px;
	}
}
.social .icon{
	width: 15px;
	height: 15px;
	margin-top: 30px;
	margin-right: 10px;
	@include iphone8(){
		margin-top: 10px;
	}
}
.social .icont{
	width: 25px;
	height: 25px;
	margin-top: 35px;
	@include iphone8(){
		margin-top: 15px;
	}
}
.social img{
	width: 100%;
}
.ftBox_two{
	display: flex;
	height: 60px;
	justify-content: space-between;
	padding: 0 20px;
	align-items: center;
	@include iphone8(){
		height: auto;
		justify-content: center;
		flex-direction: column;
	}
}
.fTitle{
	ul{
		display: flex;
		font-size: 0;
	}
	li{
		padding: 0 10px;
		text-align: center;
		border-right: 1px solid #fff;	
		a{
			font-size: 16px;	
			font-family: 微軟正黑體, sans-serif;
			color: #fff;
		}
	}
	li:last-child{
		border-right: none;
	}

	@include desktop(){
		ul{
			font-size: unset;
			li{
				a{
					font-size: 20px;
				}
			}
		}
	}

	@media (max-width: 667px){
		ul{
			li{
				font-size: 0;
				padding: 0 5px;
				a{
					font-size: 14px;
				}
			}
		}
	}

	@media (max-width: 640px){
		ul{
			li{
				font-size: 14px;
			}
		}
	}

	@include iphone8(){
		ul{
			li{
				margin-top: 20px;
				padding: 0 10px;
			}
		}
	}

	@media (max-width: 375px){
		ul{
			li{
				padding: 0 6px;
			}
		}
	}

	@include iphone5(){
		ul{
			li{
				padding: 0 5px;
				a{
					font-size: 12px;
				}
			}
		}
	}
}
.terms{
	ul{
		display: flex;
	}
	li{
			padding: 0 10px;
			text-align: center;
			color: #fff;
			border-left: 1px solid #fff;
		a{
			font-size: 16px;	
			font-family: 微軟正黑體, sans-serif;
			color: #fff;
		}
	}
	li:first-child{
		border-left: none;
		font-size: 0;
	}
	li:nth-of-type(2){
		font-size: 0;
	}
	li:last-child{
		border-left: none;
		font-size: 16px;
	}
	@include desktop(){
		ul{
			li{
				a{
					font-size: 20px;
				}
			}
			li:first-child{
				font-size: unset;
			}
			li:nth-of-type(2){
				font-size: unset;
			}
			li:last-child{
				font-size: 20px;
			}
		}
	}
	@media (max-width: 823px){
		width: 300px;
		ul{
			flex-wrap: wrap;
		}
		li:first-child{
			margin-left: 30px;
		}
	}
	@media (max-width: 667px){
		width: 210px;
		ul{
			li{
				font-size: 14px;
				a{
					font-size: 14px;
				}
			}
			li:first-child{
				margin-left: 5px;
			}
		}
	}
	@media (max-width: 640px){
		width: 210px;
		ul{
			li{
				font-size: 14px;
				a{
					font-size: 14px;
				}
			}
			li:first-child{
				margin-left: 5px;
			}
		}
	}
	@include iphone8{
		ul{
			li{
				font-size: 14px;
				a{
					font-size: 14px;
				}
			}
			li:first-child{
				margin-top: 10px;
				margin-left: 5px;
			}
			li:nth-of-type(2){
				margin-top: 10px;
			}
		}
	}
	@include iphone5{
		ul{
			li{
				a{
					font-size: 12px;
				}
			}
			li:first-child{
				margin-left: 15px;
			}
		}
	}
}
